import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html',
})
export class TabsPage {
  tab1: string = "RegisterpetsPage";
  tab2: string = "PetPage";
  tab3: string = "LostpetsPage";
  tab4: string = "OtherPage";

  constructor(public navCtrl: NavController, public navParams: NavParams, private authProvider: AuthProvider, public app: App) {
  }

  ionViewDidEnter() {
    this.authProvider.isAuthenticated().then(isAuth => {
      if(isAuth) {
        return true;
      }else {
        this.app.getRootNav().setRoot('HomePage');
      }
    });
  }
}



